@import "../node_modules//bootstrap/dist/css/bootstrap.min.css";
@import url("https://fonts.googleapis.com/css2?family=El+Messiri:wght@400;500;600;700&display=swap");
@import "../node_modules/quill/dist/quill.core.css";
@import "../node_modules/quill/dist/quill.snow.css";
@import "../node_modules/@angular/material/prebuilt-themes/indigo-pink.css";
@import "../node_modules/animate.css/animate.min.css";
@import "../node_modules/ngx-owl-carousel-o/lib/styles/prebuilt-themes/owl.carousel.min.css";
@import "../node_modules/ngx-owl-carousel-o/lib/styles/prebuilt-themes/owl.theme.default.min.css";
@import "../node_modules/primeicons/primeicons.css";
@import "../node_modules/primeng/resources/themes/saga-blue/theme.css";
@import "../node_modules/primeng/resources/primeng.min.css";
@import "../node_modules/font-awesome/css/font-awesome.min.css";
html,
body {
  height: 100% !important;
}

::-webkit-scrollbar {
  width: 4px;
  height: 5px;
}

::-webkit-scrollbar-thumb {
  background: #c0392b;
  border-radius: 20px;
}

::-webkit-scrollbar-track {
  background: #c0392b;
  border-radius: 20px;
}

::-webkit-scrollbar {
  width: 4px;
  height: 5px;
}

.scroll-transparent::-webkit-scrollbar-thumb {
  background: #c0392b;
  border-radius: 20px;
}

.scroll-transparent::-webkit-scrollbar-track {
  background: transparent !important;
  border-radius: 20px;
}

.scroll-transparent::-webkit-scrollbar {
  width: 3px;
  height: 1px;
}

* {
  outline: none !important;
}

.p-toast .p-toast-message {
  direction: rtl !important;
}

.pointer {
  cursor: pointer;
}

.box-main {
  border-radius: 5px;
  border: 1px solid #e0e0e0;
  border-top: 3px solid #c0392b;
  border-bottom: 3px solid #c0392b;
}

.main-btn {
  color: #c0392b;
  border: 3px solid #c0392b;
  border-radius: 5px;
  padding: 6px 20px;
  cursor: pointer;
  transition: 0.3s;
  text-decoration: none;
}
.main-btn:hover {
  color: white;
  background-color: #c0392b;
}

.showMore {
  height: 450px;
  overflow-y: scroll;
  overflow-x: clip;
}

.small-hr {
  width: 100%;
  display: block;
}
.small-hr::after {
  content: "";
  max-width: 50px;
  height: 3px;
  background: #c0392b;
  display: block;
  margin: auto;
  border-radius: 30px;
}

.gray-transparent::before {
  content: "";
  background: #f8f9fa;
  background: linear-gradient(0deg, #f8f9fa 0%, rgba(248, 249, 250, 0) 100%);
}

.showmore-layout {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f8f9fa+0,f8f9fa+49,ffffff+100&1+0,1+49,0+100 */
  background: -moz-linear-gradient(bottom, #f8f9fa 0%, #f8f9fa 49%, rgba(255, 255, 255, 0) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(bottom, #f8f9fa 0%, #f8f9fa 49%, rgba(255, 255, 255, 0) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to top, #f8f9fa 0%, #f8f9fa 49%, rgba(255, 255, 255, 0) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#f8f9fa", endColorstr="#00ffffff",GradientType=0 );
  /* IE6-9 */
  display: block;
  position: absolute;
  top: auto;
  bottom: 0px;
  left: 0px;
  right: 0px;
  height: 30%;
  display: flex;
  align-items: flex-end;
  z-index: 10;
}

.showmore-layout-w {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f8f9fa+0,f8f9fa+49,ffffff+100&1+0,1+49,0+100 */
  background: -moz-linear-gradient(bottom, #f8f9fa 0%, white 49%, rgba(255, 255, 255, 0) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(bottom, white 0%, white 49%, rgba(255, 255, 255, 0) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to top, white 0%, white 49%, rgba(255, 255, 255, 0) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#fffff", endColorstr="#ffffff",GradientType=0 );
  /* IE6-9 */
  display: block;
  position: absolute;
  top: auto;
  bottom: 0px;
  left: 0px;
  right: 0px;
  height: 30%;
  display: flex;
  align-items: flex-end;
  z-index: 10;
}

.small-card-img {
  height: 215px;
  width: 175px;
  border-radius: 5px;
  background-size: cover;
  background-position: center center;
}

.bold-border-top {
  border-top: 3px solid #c0392b;
}

.font-12 {
  font-size: 12px;
}

.section-banner {
  height: 15vw;
  width: 100%;
}

.section-banner-skeleton {
  height: 15vw !important;
  position: absolute !important;
  top: 0;
  z-index: -1;
}

.img-cover {
  background-size: cover;
  background-position: center center;
}

th, td {
  text-align: right !important;
}

.input-overlay {
  direction: ltr !important;
  position: absolute !important;
  bottom: -10px !important;
}

.input-overlay input {
  opacity: 0 !important;
  height: 0px !important;
  padding: 0 !important;
}

.td-actions {
  text-align: left !important;
}

.text-custom-light {
  color: #b1b1b1 !important;
}

.short-sec {
  min-height: calc( 100% - 335px );
}

.navbar-toggler {
  border: none;
}
.navbar-toggler:focus {
  box-shadow: none;
}

html,
body {
  height: 100%;
  font-family: "El Messiri", sans-serif !important;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
div,
span,
p,
a,
button li {
  font-family: "El Messiri", sans-serif !important;
}

.pi-chevron-right:before {
  content: "";
}

p-paginator-prev,
.p-paginator-prev {
  color: red !important;
}

.mat-paginator-range-actions {
  direction: rtl;
}

.mat-paginator-range-label {
  direction: ltr;
}